import React from 'react';
import Apruv from '../../Layouts/Home/Videos/Partner.mp4';

const homeyashwantImage = () => {
    return <video
        className="video-fluid z-depth-1"
        loop
        controls
        muted
        preload="none"
    >
        <source src={Apruv} type="video/mp4" />
    </video>
}
export default homeyashwantImage;